import React, { useEffect, useState } from 'react';
import { ethers } from 'ethers';

const TokenBalances = () => {
  const [balances, setBalances] = useState([]);

  useEffect(() => {
    const fetchBalances = async () => {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const walletAddress = await signer.getAddress();

      const tokenAddresses = [
        "0xdAC17F958D2ee523a2206206994597C13D831ec7" // Replace with actual token addresses
        
        // Add more token addresses as needed
      ];

      const erc20Abi = [
        "function balanceOf(address owner) view returns (uint256)"
      ];

      const getTokenBalances = async (walletAddress, tokenAddresses) => {
        const balances = await Promise.all(
          tokenAddresses.map(async (tokenAddress) => {
            const tokenContract = new ethers.Contract(tokenAddress, erc20Abi, provider);
            const balance = await tokenContract.balanceOf(walletAddress);
            return {
              tokenAddress,
              balance: ethers.utils.formatUnits(balance, 18) // Adjust decimal based on the token's decimals
            };
          })
        );
        return balances;
      };

      const balances = await getTokenBalances(walletAddress, tokenAddresses);
      setBalances(balances);
    };

    fetchBalances();
  }, []);

  return (
    <div>
      <h3>Token Balances</h3>
      <ul>
        {balances.map((balance, index) => (
          <li key={index}>
            Token Address: {balance.tokenAddress} - Balance: {balance.balance}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default TokenBalances;
